<script>
import Vuex from 'vuex';

export default {
  data() {
    return { show: 0 };
  },
  computed: {
    ...Vuex.mapState(['items']),
  },
  mounted() {},
  methods: {
    showChild(index) {
      this.show = -1;
      for (let i = 0; i < this.items.length; i += 1) {
        const item = this.items[i];
        if (item.id === index) {
          this.show = item.id;
        }
      }
    },
  },
  components: {},
};
</script>

<template>
  <aside class="pst-aside">
    <ul class="aside-items">
      <li v-for="(item,index) in items" :key="index" @click="showChild(index)">
        <a :href="item.href" :class="{active:show===index}">{{item.text}}</a>
        <!-- <transition-group tag="div" name="slideDown"> -->
        <ul
          :key="item.id"
          :style="index === show?`height: ${item.child.length*2.1}rem`:'height:0rem'"
        >
          <li v-for="(item_2nd,i) in item.child" :key="i">
            <a :href="item_2nd.href">{{item_2nd.text}}</a>
          </li>
        </ul>
        <!-- </transition-group> -->
      </li>
    </ul>
  </aside>
</template>

<style lang="scss" scoped>
$maxWidth: 1280px;
.pst-aside {
  min-height: 100vh;
  display: inline-block;
  margin-left: calc(50vw - 640px);
  border-top-left-radius: 1rem;
  margin-right: 1rem;
  overflow: hidden;
  .aside-items {
    // border-top-left-radius: 1rem;
    background: #ddd;
    li {
      text-align: center;
      a {
        display: inline-block;
        color: #666;
        padding: 0.5rem 1rem;
        width: 100%;
        &:hover {
          border-top-left-radius: 1rem;
          color: white;
          background: #888;
        }
        &.active {
          border-top-left-radius: 1rem;
          color: white;
          background-color: #888;
        }
      }
      ul {
        transition: 0.3s height;
        overflow: hidden;
        li {
          text-align: left;
          // border-bottom: 1px dashed darkgray;
          a {
            // color: black;
            // transition: 0.2s background-color;

            &:hover {
              border-top-left-radius: 0rem;
              padding-left: 0.8rem;
              // background: lightblue;
            }
          }
        }
      }
    }
  }
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-to {
  transition: 0.2s all;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
</style>
