<script>
import Vuex from 'vuex';

export default {
  data() {
    return { show: 0 };
  },
  computed: {
    ...Vuex.mapState(['tabs']),
  },
  mounted() {},
  methods: {
    showChild(index) {
      this.show = index;
    },
  },
  components: {},
};
</script>

<template>
  <div id="pst-tabs">
    <ul class="tabsTitle">
      <li v-for="(tab,index) in tabs" :key="tab.title" :class="{active:show === index}">
        <a href="javascript:;" @click="showChild(index)" @focus="showChild(index)">{{tab.title}}</a>
        <ul class="tabContents" v-show="show === index">
          <li v-for="tabContent in tab.contents" :key="tabContent.id">
            <span>{{tabContent.date}}</span>
            <a href="###">{{tabContent.title}}</a>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<style lang="scss" scoped>
$maxWidth: 1280px;
#pst-tabs {
  vertical-align: top;
  display: inline-block;
  margin-left: 1rem;
  ul.tabsTitle {
    width: 640px;
    margin: 0 auto;
    position: relative;
    text-align: left;
    > li {
      margin-right: 0.5rem;
      display: inline-block;
      > a {
        display: inline-block;
        line-height: 2rem;
        padding: 0 1.5rem;
        border: 1px solid gray;
        background-color: white;
        color: gray;
        border-top-left-radius: 1rem;
        transition: .3s all;
        // margin-bottom: 1rem;
      }
      > .tabContents {
        position: absolute;
        left: 0;
        border-right: 1px solid gray;
        border-bottom: 1px solid gray;
        border-left: 1px solid gray;
        overflow: hidden;
        border-bottom-right-radius: 1rem;
        li {
          padding: 0 2rem;
          margin: 0 0.5rem;
          span,
          a {
            display: inline-block;
            line-height: 2rem;
          }
          span {
            margin: 0 2rem 0 1rem;
          }
          &:hover {
            background: #ddd;
          }
        }
        li + li {
          border-top: 1px dashed gray;
        }
      }
      &.active {
        > a {
          border: 1px solid transparent;
          background: gray;
          color: white;
        }
      }
    }
  }
}
