<script>
import Vuex from 'vuex';

export default {
  data() {
    return { show: true };
  },
  computed: {
    ...Vuex.mapState(['items']),
  },
  mounted() {},
  methods: {
    showFooterDetail() {
      this.show = !this.show;
    },
  },
  components: {},
};
</script>

<template>
  <footer>
    <a class="switchBtn" href="###" @click="showFooterDetail">click</a>
    <ul class="footer-items">
      <li
        class="footer-item"
        v-for="item in items"
        :key="item.id"
        :style="`width:calc(1280px / ${items.length + 0.1})`"
      >
        <a :href="item.href">{{item.text}}</a>
        <ul
          style="transition:.3s height"
          :style="show?`height:${2*item.child.length}rem`:'height:0vw' "
        >
          <li v-for="(item_2nd,i) in item.child" :key="i">
            <a :href="item_2nd.href">{{item_2nd.text}}</a>
          </li>
        </ul>
      </li>
    </ul>
  </footer>
</template>

<style lang="scss" scoped>
$maxWidth: 1280px;

footer {
  width: 100%;
  text-align: center;
  background: #ddd;
  padding-bottom: 1rem;
}
.switchBtn {
  display: inline-block;
  color: #666;
  background: white;
  width: 100%;
}
ul.footer-items {
  text-align: left;
  max-width: $maxWidth;
  margin: 0 auto;
  li.footer-item {
    display: inline-block;
    vertical-align: top;
    text-align: center;
    > a {
      color: #666;
      margin-top: 1rem;
      margin-bottom: 0.5rem;
      display: inline-block;
    }
    ul {
      transition: 0.3s all;
      overflow: hidden;
      li {
        box-sizing: border-box;
        a {
          line-height: 1.8rem;
          color: #666;
        }
      }
    }
  }
}
</style>
