<script>
import Vuex from 'vuex';

export default {
  data() {
    return { show: 0, slideMode: 'slideNext' };
  },
  computed: {
    ...Vuex.mapState(['sliderList']),
  },
  mounted() {
    document.addEventListener('keyup', (e) => {
      if (e.keyCode === 39) {
        this.slideNext();
      } else if (e.keyCode === 37) {
        this.slidePrev();
      }
    });
  },
  methods: {
    slideNext() {
      const len = this.sliderList.length;
      this.slideMode = 'slideNext';
      this.show = (this.show + 1 + len) % len;
    },
    slidePrev() {
      const len = this.sliderList.length;
      this.slideMode = 'slidePrev';
      this.show = (this.show - 1 + len) % len;
    },
    slideSpot(i) {
      this.slideMode = 'fade';
      this.show = i;
    },
  },
  components: {},
};
</script>

<template>
  <div class="pst-slider">
    <div class="moveBtn prevBtn">
      <a href="javascript:;" @click="slidePrev">Prev</a>
    </div>
    <transition-group tag="ul" :name="slideMode" class="sliderItems">
      <li v-for="(sliderItem) in sliderList" :key="sliderItem.id" v-show="sliderItem.id === show">
        <img :src="sliderItem.src" :alt="sliderItem.title">
        <p>{{sliderItem.title}}</p>
      </li>
    </transition-group>
    <div class="moveBtn nextBtn">
      <a href="javascript:;" @click="slideNext">Next</a>
    </div>
    <ul class="spots">
      <li class="spot" v-for="(item,i) in sliderList" :key="item.id">
        <a
          href="javascript:;"
          :class="{active:i===show}"
          @focus="slideSpot(i)"
          @click="slideSpot(i)"
        ></a>
      </li>
    </ul>
  </div>
</template>

