/* eslint-disable no-underscore-dangle */
import Vue from 'vue';
import AppNav from './AppNav.vue';
import AppFooter from './AppFooter.vue';
import AppAside from './AppAside.vue';
import AppSlider from './AppSlider.vue';
import AppTabs from './AppTabs.vue';
import store from './store';

Vue.config.productionTip = false;
if (window.__INITIAL_STATE__) {
  store.replaceState(window.__INITIAL_STATE__);
}

new Vue({
  store,
  render: a => a(AppNav),
}).$mount('#pst-nav');

new Vue({
  store,
  render: a => a(AppAside),
}).$mount('#pst-aside');

new Vue({
  store,
  render: a => a(AppFooter),
}).$mount('#pst-footer');

new Vue({
  store,
  render: a => a(AppSlider),
}).$mount('#pst-slider');

new Vue({
  store,
  render: a => a(AppTabs),
}).$mount('#pst-tabs');

window.snapSaveState = () => {
  document.querySelector('#pst-nav').setAttribute('data-server-rendered', 'true');
  document.querySelector('#pst-aside').setAttribute('data-server-rendered', 'true');
  document.querySelector('#pst-footer').setAttribute('data-server-rendered', 'true');
  document.querySelector('#pst-slider').setAttribute('data-server-rendered', 'true');
  document.querySelector('#pst-tabs').setAttribute('data-server-rendered', 'true');
};
