<script>
import Vuex from 'vuex';

export default {
  data() {
    return { show: -1 };
  },
  computed: {
    ...Vuex.mapState(['items']),
  },
  mounted() {},
  methods: {
    showChild(index) {
      for (let i = 0; i < this.items.length; i += 1) {
        const item = this.items[i];
        if (item.id === index) {
          this.show = item.id;
        }
      }
    },
  },
  components: {},
};
</script>

<template>
  <nav class="pst-nav">
    <ul class="nav-items">
      <li
        v-for="(item,index) in items"
        :key="item.id"
        class="nav-item"
        @mouseenter="showChild(index)"
        @mouseleave="show = -1"
      >
        <a :href="item.href">{{item.text}}</a>
        <transition-group tag="div" name="fade">
          <ul class="nav-items-2nd" v-show="index===show" :key="item.id">
            <li class="nav-item-2nd" v-for="(item_2nd,index) in item.child" :key="index">
              <a :href="item_2nd.href">{{item_2nd.text}}</a>
              <ul class="nav-items-3rd">
                <li v-for="(item_3rd,i) in item_2nd.child" :key="i">
                  <a :href="item_3rd.href">{{item_3rd.text}}</a>
                </li>
              </ul>
            </li>
          </ul>
        </transition-group>
      </li>
    </ul>
  </nav>
</template>
<style lang="scss" scoped>
$maxWidth: 1280px;
.pst-nav {
  background: #888;
  margin-bottom: 1rem;

  ul.nav-items {
    max-width: $maxWidth;
    margin: 0 auto;
    text-align: right;
    li.nav-item {
      display: inline-block;
      vertical-align: top;
      position: relative;
      text-align: left;

      a {
        color: white;
        padding: 0 1rem;
        line-height: 3rem;
        display: inline-block;
        font-weight: 600;
      }
      ul.nav-items-2nd {
        position: absolute;
        background-color: #ddd;
        position: fixed;
        z-index: 9999;
        top: 3rem;
        width: 100vw;
        left: 0;
        padding-left: calc(50vw - 640px);
        padding-right: calc(50vw - 640px);
        box-sizing: border-box;
        margin-bottom: 2rem;
        li.nav-item-2nd {
          width: calc(1280px / 6);
          display: inline-block;
          vertical-align: top;
          position: relative;
          margin-top: 0.5rem;
          &::before {
            position: absolute;
            content: "";
            width: 2px;
            height: calc(100% - 1rem);
            top: 0.6rem;
            left: 0.5rem;
            background: #666;
          }
          a {
            color: #666;
            line-height: 2rem;
            font-size: 1.1rem;
          }
          ul.nav-items-3rd {
            padding-bottom: 0.5rem;
            li {
              display: list-item;
              padding-left: 0.5rem;
              a {
                line-height: 1.5rem;
                padding: 0.3rem 0.5rem;
                padding-left: 1rem;
                font-size: 1rem;
                width: 80%;
                color: black;
                font-weight: 300;
                &:hover {
                  background-color: #888;
                  color: white;
                  // font-weight: 600;
                  border-top-left-radius: 1rem;
                }
              }
            }
          }
        }
      }
    }
  }
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-to {
  transition: 0.1s all;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
</style>
